import { connect } from 'react-redux'
import { loadMovies } from '../store/actions/movieActions'
import { HomeMovieList } from '../cmps/HomeMovieList'
import { MovieFilter } from '../cmps/MovieFilter'
import { logout } from '../store/actions/userActions'
import { CinemaCard } from '../cmps/CinemaCard'
import AOS from 'aos'
import 'aos/dist/aos.css' // You can also use <link> for styles
// import {MovieSlider} from '../cmps/MovieSlider'
import { MovieSlider } from '../cmps/MovieSlider'
import Slider from '../cmps/NetflixSlider'
import { FaRegEye } from 'react-icons/fa'
import { BsInfoCircle } from 'react-icons/bs'
import { MdAccessTime } from 'react-icons/md'
