import React from 'react'
import { MoviePreview } from './MoviePreview'


export function MovieList({ movies }) {
  const genres = [
   'Action','Comedy','Crime','Drama','Fantasy',
   'Adventure','Horror','Sci-Fi','Thriller',
]
  const all = movies.slice(6, 12)

  //todo: no 'Fairy Tale' 'Romance' Historical',

 
  return (

    <div className="movie-list">
       
        <div className="movie-category">
            <div className="category-title">
                <h4 className="category-name">Popular in WatChat</h4>
                <h4 className="all-btn">See All {movies.length}</h4>
            </div>
            <div className="category-movies">
                { 
                all.map(movie => 
                <div key={ movie._id } className="movie-container">
                    <MoviePreview className="movie-preview" movie={ movie }/>
                    {/* <div className="movies-information-container">
                        <div className="movies-information-container-title">{ movie.title }</div>
                        <div className="btns-container">
                                      <span className="movie-btn movie-btn-details" ><Link to={`/movie/${movie._id}`} ><i class="fas fa-info-circle"></i></Link></span>
                                
                                <span className="movie-btn"><Link  to={`/room/${movie._id}`}><i class="far fa-play-circle"></i></Link></ span>
                                <span className="movie-btn"><Link><i class="fas fa-plus"></i></Link></ span>
                        </div>
                      
                    </div> */}
                </div>)
                }
            </div>
        </div>

        {genres.map(genre=>

            <div className="movie-category">
                <div className="category-title">
                <h4 className="category-name">{genre}</h4>
            <h4 className="all-btn">See All { movies.filter((movie) => movie.genres.includes(genre)).length}</h4>
            </div>
            <div className="category-movies">
                { 
                 movies.filter((movie) => movie.genres.includes(genre)).slice(0, 6).map(movie => 
                    <div key={ movie._id } className="movie-container">
                      <MoviePreview className="movie-preview" movie={ movie }/>
                   
                 
                    </div>
                )}
            </div>
            </div>

        )}
      
        </div>
       



    // <div className='movielist'>
    //   {thriller.map((movie) => (
    //     <MoviePreview key={movie._id} movie={movie} />
    //   ))}
    // </div>
  )
}
