import React, { Component } from 'react'

export class MovieFilter extends Component {

    render() {
        return (
            <form>
                <div className="input-icons"> 
                    <i className="fas fa-search icon"></i>
                    <input  className="input-field" placeholder="Search Movie" name="search" autoComplete="off"  onChange={ this.props.handleInputs} type="search" />
                </div> 
                <div className="input-type-container"> 
                    <select defaultValue="All" name="type" id="movieType"  onChange={this.props.handleInputs } >
                        <option value="All">All</option>
                        <option value="Action">Action</option>
                        <option value="Comedy">Comedy</option>
                        
                    </select>
                </div>
                <div className="input-number-container">
                    <div className="Min-Year-container">
                        <div className="Min-Year-title">Min Year</div>
                        <input placeholder="$" type="number" name="minYear" onChange={this.props.handleInputs } /> 
                    </div>
                   <span className="price-middle-line">-</span>
                    {/* <label htmlFor="">Max Year</label> */}
                    <div className="Max-Year-container">
                        <div className="Max-Year-title">Max Year</div>
                        <input placeholder="$$$" type="number" name="maxYear" onChange={ this.props.handleInputs } />
                    </div>
                </div> 
            </form>
        )
    }
}


