const mapStateToProps = (state) => {
  return {
    movies: state.movie.movies,
    loggedInUser: state.user.loggedInUser,
  }
}
const mapDispatchToProps = {
  loadMovies,
  logout,
}
export const Movies = connect(mapStateToProps, mapDispatchToProps)(_Movies)
